import React, { Component } from "react"
import styled from "styled-components"
import Laser from "./laser.js"
import GardenArchitecture from "./architektura-ogrodowa.js"
import Decorations from "./dekoracje.js"
import Contact from "./kontakt"
import Logo from "../components/atoms/Logo"
import LogoText from "../components/atoms/LogoText"
import Toggle from "../components/atoms/Toggle"
import Menu from "../components/molecules/Menu"
import MainContent from "../components/molecules/MainContent"
import logoText from "../assets/logo-one-line-text.svg"
import logo from "../assets/logo.svg"
// import { importAllFiles } from "../helpers/functions"
import "bootstrap/dist/css/bootstrap.min.css"
import "../index.css"

const Header = styled.header`
  position: fixed;
  width: 100%;
  height: 60px;
  align-items: center;
  padding: 0 100px;
  display: flex;
  justify-content: space-between;
  /* background: rgb(66, 69, 66, 0.6); */
  background: white;
  z-index: 1000;

  @media (max-width: 1064px) {
    padding: 0 10px;
    &.active {
      > div:before {
        top: 14px;
        transform: rotate(45deg);
        box-shadow: 0 0 0 #fff;
      }
      > div:after {
        bottom: 14px;
        transform: rotate(-45deg);
      }
    }
  }
`

const StyledSection = styled.section`
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 100vh;
  padding: 150px 100px 100px;

  @media (max-width: 1064px) {
    padding: 100px 50px 50px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    display: flex;
  }
`

const ContactSection = styled.section`
  background: white;
  min-height: 150px;
  display: flex;
  justify-content: space-between;
  padding-left: 20px;

  @media (max-width: 555px) {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
`

class Home extends Component {
  state = {
    // photos: importAllFiles(
    //   require.context(
    //     "../assets/photos/transparent_laser",
    //     false,
    //     /\.(png|jpe?g|svg)$/
    //   )
    // ),
    activeMenu: false,
  }
  toggle = () => {
    this.setState(prevState => ({
      activeMenu: !prevState.activeMenu,
    }))
  }

  render() {
    return (
      <>
        <Header className={this.state.activeMenu ? "active" : ""} id="header">
          <LogoText src={logoText} alt="Firma z drewna" to="/" />
          <Menu
            callback={() => this.toggle()}
            activeMenu={this.state.activeMenu}
          />
          <Toggle callback={() => this.toggle()} />
        </Header>
        <StyledSection id="home">
          <MainContent />
          <Logo src={logo} alt="Logo" />
        </StyledSection>
        <StyledSection id="laser">
          <Laser />
        </StyledSection>
        <StyledSection id="garden-architecture">
          <GardenArchitecture />
        </StyledSection>
        <StyledSection id="decorations">
          <Decorations />
        </StyledSection>
        <ContactSection id="contact">
          <Contact />
        </ContactSection>
      </>
    )
  }
}

export default Home
